<template>
    <div>
        <top-header :active="'1'"></top-header>
        <div class="top"></div>
        <!-- <div>
            <img src="require('../../assets/img/banner1.jpg')" alt="">
        </div> -->
        <img class="banner" :src="require('../../assets/img/banner1.jpg')" alt="">
       <div  class="wow animate__flipInX tit" data-wow-duration="1s" data-wow-delay="1s">
           产品优势
       </div>
       <div class="productGoodBox">
           <div v-for="(item,index) in DproductGood" class="wow animate__zoomIn" data-wow-duration="1s" data-wow-delay="1s" :key="index">
               <img :src="item.imgSrc" alt="">
               <h3>{{item.tit}}</h3>
               <p>{{item.pInfo}}</p>
            </div>
       </div>

       <div  class="wow animate__flipInX tit" data-wow-duration="1s" data-wow-delay="1s">
           技术理念
       </div>
       <div class="technologyBox">
           <div class="wow animate__zoomIn" data-wow-duration="1s" data-wow-delay="1s" >
               <div style="height:130px"></div>
               <img :src="require('../../assets/img/tubiaoJS.png')" alt="">
               <h4>技术架构</h4>
               <p>
                   本质上是一系列物联网微服务的集合，采用分布式架构，应用程序和服务组件均不存在单点风险，针对不同级别的业务场景可随时扩容，企业无需关心高并发、稳定性等技术难点
               </p>
           </div>
           <div class="wow animate__zoomIn" data-wow-duration="1s" data-wow-delay="1s" >
               <img class="right" :src="require('../../assets/img/技术架构.png')" alt="">
           </div>
       </div>
       <div class="technologyBox technologyBoxtwo">
           <div class="wow animate__zoomIn" data-wow-duration="1s" data-wow-delay="1s" >
               <img class="right" :src="require('../../assets/img/使用流程.png')" alt="">
           </div>
           <div class="wow animate__zoomIn TRPdivBox">
               <div style="height:130px"></div>
               <img :src="require('../../assets/img/tubiaoJS2.png')" alt="">
               <h4>使用流程</h4>
               <p class="TRP">
                   用户只需登录可视化控制台，编写少量代码，即可快速实现设备上云和应用开发。企业基于 IoTOS 的一站式标准接入能力，可大大减少技术投入，甚至可围绕 IoTOS 发展产品供应和外包研发的合作生态
               </p>
           </div>
       </div>

       <div  class="wow animate__flipInX tit" data-wow-duration="1s" data-wow-delay="1s">
           功能展示
       </div>

        <div class="modularBox">
            <div class="modular">
                <div v-for="(item,index) in Dmodular" :class="{modular_active:activeName==index}" @click="Fnmodular(index)" :key='index'>
                    <svg class="icon iconDefault">
                        <use :xlink:href="item.iconD"></use>
                    </svg>
                    <h4>{{item.text}}</h4>
                </div>
            </div>
        </div>

        <div class="modularContent">
            <div>
                <img :src="require('../../assets/img/banner3.jpg')" alt="">
            </div>
            <div class="MCtext">
                <h4>Data Management</h4>
                <ul>
                    <li v-for="(Citem,Cindex) in DmodularContent" :key="Cindex">
                        <svg class="icon iconDefault">
                            <use xlink:href="#icon-yuandian"></use>
                        </svg>
                        {{Citem}}
                    </li>
                </ul>
            </div>
        </div>

        <div class="footer">
            © 黄岛达摩智联科技有限公司版权所有 浙ICP备15005406号-1
        </div>
    </div>
</template>
<script>
import {WOW} from 'wowjs'
import topHeader from './Header'
export default {
    data() {
        return {
            DproductGood:[//产品优势列表
                {
                    imgSrc:require('../../assets/img/tubiao.png'),
                    tit:'高性能高可靠',
                    pInfo:'支持大规模分布式部署，千万级设备连接和数十万级 QPS 请求处理能力，毫秒级延迟，可支撑海量物联网设备接入需求'
                },
                {
                    imgSrc:require('../../assets/img/tubiao2.png'),
                    tit:'规则引擎',
                    pInfo:'提供基于 SQL 的高速规则引擎，支持用户设置复杂规则实时过滤海量数据，减少应用侧压力，使客户聚焦业务开发'
                },
                {
                    imgSrc:require('../../assets/img/tubiao3.png'),
                    tit:'数据实时分析',
                    pInfo:'内置高性能分布式时序数据库，支持海量数据的持久化和实时分析，也支持与用户提供的数据库、大数据平台或消息列对接，方便用户对数据进行存储与消费'
                },
            ],
            activeName: 1,//功能选择第几个
            Dmodular:[
                {
                    iconD:'#icon-lianjieconnecting5',
                    text:'连接管理'
                },
                {
                    iconD:'#icon-shebei',
                    text:'设备管理'
                },
                {
                    iconD:'#icon-data',
                    text:'数据管理'
                },
                {
                    iconD:'#icon-kuaisu_jiasu',
                    text:'应用使能'
                },
                {
                    iconD:'#icon-computer_icon',
                    text:'后台管理'
                },
            ],
            DmodularContent:[
                "系统总体分析：包括但不限于设备总量、在线离线比例等",
                "地理分布分析：统计设备的地理分布并在地图上展示",
                "指标趋势分析：对设备指标原始值进行展示，支持百亿条级日志规模",
                "指标聚合分析：对设备指标聚合值进行展示，支持百亿条级日志规模",

            ]
        }
    },
    components:{
        topHeader
    },
    methods: {
        Fnmodular(index){
            this.activeName=index
        }
    },
    mounted() {
        var wow = new WOW({//这是wowjs插件，使用标志滚动
            boxClass: 'wow',
            animateClass: 'animated',
            offset: 150,
            mobile: true
        })
        wow.init();
    },
}
</script>
<style lang='stylus' scoped>
    .top
        height 92px
    .banner
        width 100%
        height 508px
        margin-bottom 50px
    .tit
        font-size 52px
        color #000
        font-weight bold
        text-align center
        line-height 300px
    .productGoodBox
        width 1200px
        margin 0 auto
        display flex
        div
            flex 1
            margin 0 25px 50px
            text-align center
            p
                color: #6f6f6f;
                line-height 30px
                margin 0 15px 20px
            h3
                font-size 22px
                font-weight bold
                margin 35px 0px 25px
            img 
                margin-top 60px
        div:hover
            box-shadow: 0px 0px 8px 8px #f1f1f1;
    .technologyBox
        width 1200px
        margin 0 auto 120px
        display flex
        div
            flex 1
            .right 
                width 831px
                height 500px
            h4 
                font-size 30px
                font-weight bold
                line-height 100px
            p 
                margin-right 30px
                font-size 22px
                color #666
                line-height 36px
            .TRP
                margin-right 0px
        .TRPdivBox
            margin-left 30px
    .technologyBoxtwo
        margin-bottom 0px
    
    
    
    .modularBox
        border-bottom 1px dashed #1767e4
        .modular
            width 950px
            margin 0 auto
            display flex
            div 
                flex 1
                text-align center
                .iconDefault
                    width 30px
                    height 30px
                    padding 20px
                    border-radius 50%
                    background rgba(23, 103, 228, 0.15)
                h4 
                    line-height 60px
                    font-size 16px
                    font-weight bold
                    font-size #333
            .modular_active
                border-bottom 3px solid #1767e4
                h4
                    color #1767e4
    .modularContent
        width 1100px
        margin 45px auto 0
        display flex
        img 
            width 370px
            height 290px
            border-radius 6px
        .MCtext
            margin-left 60px
            h4 
                font-size 24px
                font-weight bold
                line-height 60px
            li 
                font-size 18px
                color #666
                line-height 40px
    .footer
        background rgb(1,21,54)
        line-height 60px
        color #ffffff
        text-align center
        margin-top 50px
</style>


